$primary-color: #2196F3;
$secondary-color: #2196F3;

.primary-color {
  background-color: $primary-color;
}

html, body {
  font-family: 'Roboto', 'Helvetica', sans-serif;
  height: 100%;
}

.full-height {
  height: 100%;
  max-height: 100%;
}

.full-width {
  width: 100%;
  max-width: 100%;
  margin: 0;
  padding: 0;
}

.provider-avatar {
  width: 48px;
  height: 48px;
  border-radius: 24px;
}

.brand-logo {
  margin-top: 6%;
}